     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.ui-ripple-ink__ink.is-held {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.ui-ripple-ink__ink.is-done {
  opacity: 0!important;
}

.ui-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
